import request from '../utils/request'
import qs from 'qs'

let config = {
  baseURL: 'http://192.168.0.251:88'
}

export function unifiedAli(data) {
  return request({
    method: 'post',
    // url:config.baseURL + '/scanpay/unified',
    url: '/scanpay/alpH5',
    data
  })
}


/**
 * 是否支付成功
 *
 * @date 2019/7/1
 * @author nan
 */
export function isPay(phone, course_id) {
  return request({
    method: 'get',
    url: config.baseURL + '/Marketingcourse/Nineyuan/isPay?course_id=' + course_id + '&phone=' + phone
  })
}

/**
 * 获取验证码
 * http://192.168.0.251:88/Marketingcourse/Nineyuan/getBindVerCode
 * GET  参数 phone
 *
 * @date 2019/6/21
 * @author nan
 */
export function getBindVerCode(phone) {
  console.log('nan 获取验证码请求的地址：', config.baseURL + '/Marketingcourse/Nineyuan/getBindVerCode?phone=' + phone)
  return request({
    method: 'get',
    url: config.baseURL + '/Marketingcourse/Nineyuan/getBindVerCode?phone=' + phone
  })
}

/**
 *  https://api.luboedu.cn/1.0/Onlinequestion/tanglang
 *  phone: res.data,
 *  courseName: '自考',
 *  buName: '北京事业部',
 *  sourceType: [ '百度' ,'360' ,'搜狗' , '神马' , '广点通' , '今日头条' ]
 *  seaName: that.data.city
 *
 * @date 2019/7/1
 * @author nan
 */
export function getPhoneData(phone, seaName) {
  let params = []
  params.phone = phone
  params.courseName = '愈见心理研习社'
  params.buName = '北京事业部'
  params.sourceType = 'H5'
  params.seaName = seaName
  console.log('nan getPhoneData', 'https://api.luboedu.cn/1.0/Onlinequestion/tanglang?' + qs.stringify(params))
  return request({
    method: 'get',
    url: 'https://api.luboedu.cn/1.0/Onlinequestion/tanglang?' + qs.stringify(params)
  })
}

/**
 * 登录/注册
 * http://192.168.0.251:88/Marketingcourse/Nineyuan/bindPhone
 * 参数  'phone', 'vercode'   方式get
 *
 * @date 2019/6/21
 * @author nan
 */
export function loginPhone(phone, code, course_id) {
  console.log('nan 登录请求的地址：', config.baseURL + '/Marketingcourse/Nineyuan/bindPhone?course_id=' + course_id + '&phone=' + phone + '&vercode=' + code)
  return request({
    method: 'get',
    url: config.baseURL + '/Marketingcourse/Nineyuan/bindPhone?course_id=' + course_id + '&phone=' + phone + '&vercode=' + code
  })
}

/**
 * 创建阿里订单
 *
 * http://192.168.0.251:88/Marketingcourse/Nineyuan/createAliPay
 *
 * @date 2019/6/26
 * @author nan
 * @param data'price', 'out_trade_no', 'subject', 'quitUrl'
 */
export function createAliPay(data) {
  console.log('nan 绑定订单请求的地址：', 'http://192.168.0.251:88/Marketingcourse/Nineyuan/createAliPay', data)
  return request({
    method: 'post',
    url: config.baseURL + '/Marketingcourse/Nineyuan/createAliPay',
    data: data
  })
}



